@import '../../index.scss';

.container {
  height: 100vh;

  text-align: center;
}

.flex_box {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.nav_button {
  display: none;

  @media screen and (min-width: 768px) {
    transform: translateY(-50%);
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.nav_button_right {
  position: absolute;
  top: 50%;
  right: 0;
}

.nav_button svg {
  transition: transform 0.3s ease;
}

.nav_button:hover svg {
  transform: scale(1.3);
}

.button_text {
  display: none;

  @media screen and (min-width: 1200px) {
    display: block;
    position: absolute;
    left: 100%;
    transition: all 0.3s ease;
    white-space: nowrap;
    visibility: hidden;
    opacity: 0;

    font-size: 1.2em;
    line-height: 1em;
    font-weight: 500;
    letter-spacing: -1px;
    color: var(--secondary-text-color);
    font-family: var(--font);
  }
}

.custom_button:hover .button_text {
  left: calc(100% - 130px);
  visibility: visible;
  opacity: 1;
}

.textBox {
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    width: 498px;
  }

  @media screen and (min-width: 1200px) {
    width: 800px;
  }
}

.tittle {
  margin-bottom: 10px;

  font-size: 1.3em;
  line-height: 1.3em;
  font-weight: 700;
  letter-spacing: -1px;
  color: var(--secondary-text-color);
  font-family: var(--font);

  @media screen and (min-width: 768px) {
    font-size: 1.5em;

    margin-bottom: 15px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 25px;

    font-size: 1.8em;
  }
}

.text {
  margin-bottom: 10px;

  font-size: 1em;
  line-height: 1.3em;
  font-weight: 400;
  font-family: var(--font);

  @media screen and (min-width: 768px) {
    margin-bottom: 15px;

    font-size: 1.2em;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 25px;

    font-size: 1.5em;
  }
}

.button {
  position: relative;
  margin: 0 auto;
  padding: 12px 18px;
  transition: all 0.2s ease;
  border: none;
  background: none;
}

.button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  border-radius: 50px;
  background: #15a7d3;
  width: 45px;
  height: 45px;
  transition: all 0.3s ease;
}

.button span {
  position: relative;
  font-family: 'Ubuntu', sans-serif;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0.05em;
  color: var(--color-white);
}

.button svg {
  position: relative;
  top: 0;
  margin-left: 10px;
  fill: none;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke: #234567;
  stroke-width: 2;
  transform: translateX(-5px);
  transition: all 0.3s ease;
}

.button:hover:before {
  width: 100%;
  background: #15a7d3;
}

.button:hover svg {
  transform: translateX(0);
}

.button:active {
  transform: scale(0.95);
}

.wrapper {
  display: flex;
  gap: 20px;

  padding-top: 50px;
  font-family: 'Poppins', sans-serif;
  justify-content: center;

  margin-top: auto;

  @media screen and (min-width: 768px) {
    display: flex;
    gap: 30px;
  }
}

.wrapper .icon {
  position: relative;
  background: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);

  @media screen and (min-width: 768px) {
    width: 60px;
    height: 60px;
  }
}

.wrapper .tooltip {
  position: absolute;
  top: 0;
  font-size: 12px;
  color: #fff;
  padding: 5px 8px;
  border-radius: 5px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .tooltip::before {
  position: absolute;
  content: '';
  height: 8px;
  width: 8px;
  background: #fff;
  bottom: -3px;
  left: 50%;
  transform: translate(-50%) rotate(45deg);
  transition: all 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.wrapper .icon:hover .tooltip {
  top: -45px;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.wrapper .icon:hover span,
.wrapper .icon:hover .tooltip {
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.1);
}

.wrapper .github:hover,
.wrapper .github:hover .tooltip,
.wrapper .github:hover .tooltip::before {
  background: #1da1f2;
  color: #fff;
}

.wrapper .linkedin:hover,
.wrapper .linkedin:hover .tooltip,
.wrapper .linkedin:hover .tooltip::before {
  background: #1877f2;
  color: #fff;
}

.wrapper .telegram:hover,
.wrapper .telegram:hover .tooltip,
.wrapper .telegram:hover .tooltip::before {
  background: #1877f2;
  color: #fff;
}

.icon_contact {
  width: 40px;
  height: 40px;

  @media screen and (min-width: 768px) {
    width: 60px;
    height: 60px;
  }

  &:hover,
  &:focus {
    scale: 1.2;
  }
}
